// =============================================================================
// > LAYOUTS: SITE HEADER
// =============================================================================

.site-header {
    z-index: 999;
    @include fixed(left 0);
    padding: spacing(sm);
    @include font(12px white regular uppercase);
    letter-spacing: 1px;


    &--top {
        top: 0; }
    &--bottom {
        bottom: 0; }


    // ==================================================
    // > BRAND
    // ==================================================
    &__item {
        display: block;

        &__lighter {
            opacity: .4;
            padding-right: 5px; }

        &__smaller {
            @include font(10px white); } } }



