// =============================================================================
// > LAYOUTS : SITE INTRO
// =============================================================================

.site-intro {
    position: relative;
    height: 100vh;
    @include flex(row wrap, center, center);

    @include screen(sm) {
        height: auto; }

    .container {
        margin: inherit; }


    &__wrapper {
        @include flex(row-reverse wrap, center, center);

        @include screen(sm) {
            flex-direction: column; } }

    &__visual {
        flex: 0 0 600px;
        @include size(600px);
        background: rgba(white, .1);
        cursor: pointer;

        @include screen(sm) {
            flex: 1; } }

    &__desc {
        flex: 1;
        padding-right: 50px; }

    &__title {
        @include font(60px 0.9);

        @include screen(sm) {
            @include font(40px 1); } } }



